import LoginVue from "../../login/Login.vue";
export default {
  name: "GMrepControl",

  data() {
    return {
      id: "",
      key: "",
      form: {
        control_rate: "",
        frist_charge_rate: "",
        frist_charge_weight: "",
        charge_rate: "",
        charge_weight: "",
        exchange_rate: "",
        exchange_weight: "",
        new_player_rate: "",
        new_player_weight: "",
        user_guide_rate: "",
        user_guide_weight: "",
        range_control_rate: []
      },
      rules: {
        control_rate: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        frist_charge_rate: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        frist_charge_weight: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        charge_rate: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        charge_weight: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        exchange_rate: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        exchange_weight: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        new_player_rate: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        new_player_weight: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ]
      },
      formLabelWidth: "150px"
    };
  },
  created() {
    this.initData({ key: "personal_control.lua" });
  },
  methods: {
    addItem() {
      this.form.range_control_rate.push({});
    },
    deleteItem(index) {
      this.form.range_control_rate = this.form.range_control_rate.filter(
        (item, n) => {
          return index !== n;
        }
      );
    },
    send(formName, type) {
      this.$refs[formName].validate(async (valid, x) => {
        if (valid) {
          // console.log(this.form);
          if (type === 1) {
            //put
            let { data } = await this.$http.HallFunConfig.PutServerConfig({
              keys: this.key,
              values: JSON.stringify(this.form),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg) {

              this.$message({
                type: "success",
                message: "保存成功"
              });
            }
          } else if (type === 2) {
            // post
            let { data } = await this.$http.HallFunConfig.PostServerConfig({
              keys: this.key,
              values: JSON.stringify( this.form ),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg) {

              this.$message({
                type: "success",
                message: "发送服务器配置成功"
              });
            }
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    async initData(params) {
      let { data } = await this.$http.HallFunConfig.GetServerConfig(params);
      // console.log(data);
      this.key = data.data[0].sys_key;
      this.id = data.data[0].id;
      this.form = JSON.parse(data.data[0].sys_val);
      // console.log(this.form);
    }
  }
};



